import React, { useState } from 'react';
import { Link } from "react-router-dom";
import axios from 'axios';
import AdminMenu from './AdminMenu';
import Back_URL, { propShowCount } from '../validation/BackendUrl';

export default function EditSearch() {
    const [skipCnt, setSkipCnt] = useState(0);
    const [propListLoad, setPropListLoad] = useState(false);
    const [properties, setProperties] = useState([]);
    const [showMoreBtn, setShowMoreBtn] = useState(false);
    const [noPeoperty, setNoPeoperty] = useState(false);

    const [byID, setByID] = useState('');
    const [byIPlace, setByIPlace] = useState('');
    const [errors, setErrors] = useState({
        byID: ''
    });
    let searchData = {skip: skipCnt}
    const searchPro = (e) => {
        e.preventDefault();
        let errorCount = 0;
        if(byID === '' && byIPlace === '') {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, byID: 'Please Enter Id or place'}
            });
        } else {
            setErrors((prevState)=> {
                return {...prevState, byID: ''}
            });
        }
        if(errorCount === 0) {
            if(byID) {
                searchData.byID = byID;
            } else {
                searchData.byIPlace = byIPlace;
            }
            let token = localStorage.getItem("admin");
            if (token) {
                setPropListLoad(true);
                setProperties([]);
                setSkipCnt(0);
                axios.post('api/editproperty', searchData, {
                    headers: {
                        "x-access-token" : token,
                        "Content-Type" : "application/json"
                    }
                }).then((res) => {
                    setNoPeoperty(false);
                    setProperties(res.data);
                    setPropListLoad(false);
                    if(res.data.length === propShowCount) {
                        setShowMoreBtn(true);
                        setSkipCnt(skipCnt+propShowCount)
                    } else {
                        setShowMoreBtn(false);
                    }
                    if(res.data.length > 0) {
                        setNoPeoperty(false);
                    } else {
                        setNoPeoperty(true);
                    }
                })
                .catch((err) => {
                    setPropListLoad(false);
                    setShowMoreBtn(false);
                    setNoPeoperty(true);
                });
            }
        }
    }
    const PropLoader = () => {
        return (
            <div className='row listing_loader'>
                <div className='col-sm-6 col-md-4 col-lg-3'>
                    <div className='property-item'>
                        <div className='skeleton_loading prop_size'>&nbsp;</div>
                        <div className='skeleton_loading prop_heading'>&nbsp;</div>
                        <div className='skeleton_loading prop_sub_heading'>&nbsp;</div>
                        <div className='skeleton_loading prop_sub_heading'>&nbsp;</div>
                    </div>
                </div>
                <div className='col-sm-6 col-md-4 col-lg-3'>
                    <div className='property-item'>
                        <div className='skeleton_loading prop_size'>&nbsp;</div>
                        <div className='skeleton_loading prop_heading'>&nbsp;</div>
                        <div className='skeleton_loading prop_sub_heading'>&nbsp;</div>
                        <div className='skeleton_loading prop_sub_heading'>&nbsp;</div>
                    </div>
                </div>
                <div className='col-sm-6 col-md-4 col-lg-3'>
                    <div className='property-item'>
                        <div className='skeleton_loading prop_size'>&nbsp;</div>
                        <div className='skeleton_loading prop_heading'>&nbsp;</div>
                        <div className='skeleton_loading prop_sub_heading'>&nbsp;</div>
                        <div className='skeleton_loading prop_sub_heading'>&nbsp;</div>
                    </div>
                </div>
                <div className='col-sm-6 col-md-4 col-lg-3'>
                    <div className='property-item'>
                        <div className='skeleton_loading prop_size'>&nbsp;</div>
                        <div className='skeleton_loading prop_heading'>&nbsp;</div>
                        <div className='skeleton_loading prop_sub_heading'>&nbsp;</div>
                        <div className='skeleton_loading prop_sub_heading'>&nbsp;</div>
                    </div>
                </div>
            </div>
        );
    };
    const loadMore = ()=> {
        if(byID) {
            searchData.byID = byID;
        } else {
            searchData.byIPlace = byIPlace;
        }
        let token = localStorage.getItem("admin");
        if (token) {
            setPropListLoad(true);
            axios.post('api/editproperty', searchData, {
                headers: {
                    "x-access-token" : token,
                    "Content-Type" : "application/json"
                }
            }).then((res) => {
                setNoPeoperty(false);
                setProperties(oldArray => [...oldArray, ...res.data]);
                setPropListLoad(false);
                if(res.data.length === propShowCount) {
                    setShowMoreBtn(true);
                    setSkipCnt(skipCnt+propShowCount)
                } else {
                    setShowMoreBtn(false);
                }
            })
            .catch((err) => {
                setPropListLoad(false);
                setShowMoreBtn(false);
            });
        }
    }
    return (
        <div className="admin-pages container">
            <div className="row">
                <div className="col-md-3">
                    <AdminMenu />
                </div>
                <div className='col-md-9'>
                    <h3 className="mb-4">Search and Edit</h3>
                    <form className="form-row" onSubmit={searchPro}>
                        <div className="form-group mb-2">
                            <input type="text" value={byID} onChange={(e)=>setByID(e.target.value)} type="text" className="form-control" placeholder="By ID" />
                            <span className="form_err">{errors.byID}</span>
                        </div>
                        <div className="form-group mx-sm-3 mb-2">
                            <input type="text" value={byIPlace} onChange={(e)=>setByIPlace(e.target.value)} className="form-control" placeholder="By City" />
                        </div>
                        <div className="form-group mb-2">
                            <button type="submit" className="btn btn-primary mb-2">Search</button>
                        </div>
                    </form>
                    {!noPeoperty?<div className="container-fluid">
                        {properties.length > 0?<div className='row'>
                            {properties.map((list, index) =>
                                <div key={list.id} className="col-sm-6 col-md-4 col-lg-3">
                                    <div className={list.user_upload_status === "open"?"property-item":"property-item disabled"}>
                                        <div className="pi-image">
                                            <img src={Back_URL+list.img} alt={"image "+(index+1)} />
                                            {list.status === "New"?<div className="pi-badge new">New</div>:list.status === "Offer"?<div className="pi-badge offer">Offer</div>:""}
                                        </div>
                                        <h3><i className="fas fa-rupee-sign"></i>{list.price.toLocaleString()}{list.want_to==="rent_out"?<span className='per_month'>/Month</span>:""}</h3>
                                        {list.property_type === "Private"?<h5>{list.bedrooms !== "N/A" && list.bedrooms !== "1"? list.bedrooms+" Bedrooms": list.bedrooms === "1"? list.bedrooms+" Bedroom":""}  {list.house_type}</h5>:
                                        list.property_type === "Commercial"?<h5>{list.patio === "N/A"?list.patio:list.patio+" SF"} {list.house_type}</h5>:
                                        <h5>{list.property_type}</h5>}
                                        {list.property_type === "Private" || list.property_type === "Upcoming Private Project"?
                                        <div className="pi-metas">
                                            {list.bedrooms !== "N/A"?<div className="pi-meta">{list.bedrooms} Bed</div>:""}
                                            {list.bathrooms !== "N/A" && list.bathrooms !== "No"?<div className="pi-meta">{list.bathrooms} Bath</div>:""}
                                            {list.parking_type === "Garage"?<div className="pi-meta">Garage</div>:""}
                                            <div className="pi-meta">{list.property_area+" SF"}</div>
                                        </div>:
                                        <div className="pi-metas"><div className="pi-meta">For {list.hse_for}</div></div>}
                                        <Link to={"/property/"+list.id} className="edit-btn" target="_blank">View</Link>&nbsp;&nbsp;&nbsp;&nbsp;
                                        <Link to={"/editprops/"+list.id} className="edit-btn">Edit</Link>
                                    </div>
                                </div>
                            )}
                        </div>:""}
                        {propListLoad?<PropLoader />:showMoreBtn?<button className='site-btn sb-big load-more' onClick={loadMore}>Load More</button>:""}
                    </div>:<div className="container">

                        <div className="row">

                            <div className="col-12">
                                <div className="error-page text-center">
                                    <div style={{height:"60px"}}>&nbsp;</div>
                                    <h5 className="not_fnd_hd">OOPS... No Property Found!</h5>
                                    <p className="not_fnd_body">
                                        Try to Search some other Property<br />
                                        <Link to="/" className="readmore-btn">Back to Home Page</Link>
                                    </p>
                                </div>
                            </div>

                        </div>
                    </div>}
                </div>
            </div>
        </div>
    )
}